import Groq from 'groq-sdk'
import { GoogleGenAI } from '@google/genai'
import { ConversationBlock, IMemoryProvider, ProviderConfiguration } from './MemoryProviders'

export default class GeminiProvider implements IMemoryProvider {
	private api: GoogleGenAI
	private lastToken: string = ''

	constructor() {
		this.api = new GoogleGenAI({ apiKey: "YOUR_API_KEY" })
	}

	async init() {

	}

	async send(setting: Record<string, any>, history: ConversationBlock[], onUpdate: (conversation: ConversationBlock) => void, playerName: string): Promise<void> {
		try {
			if (this.lastToken !== setting.Token) {
				this.lastToken = setting.Token
				this.api = new GoogleGenAI({ apiKey: setting.Token })
			}

			history = [...history]

			for (const index in history) {
				if (history[index].meta) {
					delete history[index].meta
				}
			}

			const start = Date.now()
			const system = history.shift()?.content

			const chatCompletion = await this.api.models.generateContentStream({
				model: setting.Model,
				contents: history.map(h => ({
					role: h.role,
					parts: [{ text: h.content }]
				})),
				config: {
					maxOutputTokens: setting['Max Tokens'],
					temperature: setting.Temperature,
					systemInstruction: system
				}
			})

			let target = ''
			
			for await (const chunk of chatCompletion) {
				target += chunk.text
				const usage = chunk.usageMetadata
				onUpdate({
					role: 'assistant',
					content: target,
					meta: usage ? {
						completionTokens: usage.totalTokenCount ?? 0 - (usage.promptTokenCount ?? 0),
						contextTokens: usage.promptTokenCount ?? 0,
						delay: (Date.now() - start) / 1000
					} : undefined
				})
			}
		}
		catch (e) {
			console.log('error', e)
			console.log(e instanceof Groq.APIError)
			if (e instanceof Groq.APIError) {
				throw new Error(`${e.error.error.code}: ${e.error.error.message}`)
			}
			else {
				throw e
			}
		}
	}

	renderControls(): Record<string, ProviderConfiguration> {
		return {
			Token: {
				type: 'text',
				default: '',
				// description: 'This is never saved for security reasons.'
			},
			Model: {
				type: 'select',
				default: 'gemini-2.0-flash',
				options: [
					'gemini-2.5-flash-preview-05-20', 
					'gemini-2.5-pro-preview-06-05',
					'gemini-2.0-flash', 
					'gemini-2.0-flash-lite'
				]
			},
			'Max Tokens': {
				type: 'number',
				default: 512,
				min: 0,
				max: 4096
			},
			Temperature: {
				type: 'range',
				default: 1,
				min: 0,
				max: 2,
				step: 0.1
			}
		}
	}
}