import { useCallback, useContext, useState } from 'react'
import Container from 'react-bootstrap/Container'
import Modal from 'react-bootstrap/Modal'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import NavDropdown from 'react-bootstrap/NavDropdown'
import Button from 'react-bootstrap/Button'
import Form from 'react-bootstrap/Form'
import InputGroup from 'react-bootstrap/InputGroup'
import Alert from 'react-bootstrap/Alert'
import { Session } from './util/ApiClient'
import { ApiContext } from './util/contexts'
import { AxiosError } from 'axios'

function MainNav({ session, assumeAs: assumedAs, tab, setTab }: { trigger: number, session?: Session, assumeAs?: string, tab: string, setTab: (tab: string) => void }) {
	const [loginModalOepn, setLoginModalOpen] = useState(false)
	const [isRegistering, setIsRegistering] = useState(false)
	const [username, setUsername] = useState('')
	const [password, setPassword] = useState('')
	const [loginErrorMsg, setLoginErrorMsg] = useState('')

	const [assumeModalOpen, setAssumeModal] = useState(false)
	const [assumeText, setAssumeText] = useState('')

	const apiClient = useContext(ApiContext)

	const openModal = useCallback((isRegister: boolean) => {
		setLoginModalOpen(true)
		setIsRegistering(isRegister)
		setUsername('')
		setPassword('')
		setLoginErrorMsg('')
	}, [])

	const handleAssumeClick = useCallback(() => {
		if (assumedAs) {
			console.log('unassume')
			apiClient.unassumeUser()
		}
		else {
			setAssumeModal(true)
			setAssumeText('')
		}
	}, [apiClient, assumedAs])
	

	const handleSend = async () => {
		try {
			if (isRegistering) {
				await apiClient.registerAndLogin(username, password)
			}
			else {
				await apiClient.login(username, password)
			}
		}
		catch(e) {
			const error = e as AxiosError
			console.error(error)
			setLoginErrorMsg((error.response?.data ?? error.message) as string)
			return
		}

		setUsername('')
		setPassword('')
		setLoginModalOpen(false)
	}

	const userDisplay = session ? `${session.username}(${assumedAs ? `Assumed as ${assumedAs}` : ['User', 'Admin', 'Admin'][session.type]})` : "Not Signed in"

	return (
		<div>
			<Navbar className="bg-body-tertiary dark">
				<Container>
					<Navbar.Brand onClick={() => setTab('home')}>MW Express Client</Navbar.Brand>
					<Navbar.Toggle />
					<Navbar.Collapse id="basic-navbar-nav">
						<Nav className="me-auto">
							<Nav.Link active={tab === 'data'} onClick={() => setTab('data')}>Data</Nav.Link>
							{/* <Nav.Link active={tab === 'chat'} onClick={() => setTab('chat')}>Chat</Nav.Link> */}
							<Nav.Link active={tab === 'trainer'} onClick={() => setTab('trainer')}>Memory</Nav.Link>
							<Nav.Link active={tab === 'query'} disabled={session?.type !== 2} onClick={() => setTab('query')}>Query</Nav.Link>
						</Nav>
					</Navbar.Collapse>
					<Navbar.Collapse className="justify-content-end">
						<NavDropdown title={userDisplay} id="basic-nav-dropdown" className="justify-content-end" align="end">
							{session ? [
								session.type === 2 
									? <NavDropdown.Item onClick={handleAssumeClick} key='assume'>{assumedAs ?  "Unassume" : "Assume As"}</NavDropdown.Item> 
									: null,
								<NavDropdown.Item onClick={() => apiClient.signOut()} key='signout'>Sign out</NavDropdown.Item>
							] : [
								<NavDropdown.Item onClick={() => openModal(true)} key='register'>Register</NavDropdown.Item>,
								<NavDropdown.Item onClick={() => openModal(false)} key='signin'>Sign in</NavDropdown.Item>
							] }
						</NavDropdown>
					</Navbar.Collapse>
				</Container>
			</Navbar>
			<Modal show={loginModalOepn} onHide={() => setLoginModalOpen(false)}>
				<Modal.Header closeButton>
					<Modal.Title>{isRegistering ? "Register" : "Sign in"}</Modal.Title>
				</Modal.Header>
				<Modal.Body>
					{loginErrorMsg ? <Alert variant="danger">{loginErrorMsg}</Alert> : null}
					<Form onSubmit={e => { handleSend(); e.preventDefault()}}>
						<InputGroup className="mb-3">
							<Form.Control
								placeholder="username"
								aria-label="username"
								value={username}
								onChange={e => setUsername(e.target.value)}
							/>
						</InputGroup>
						<InputGroup className="mb-3">
							<Form.Control
								placeholder="Password"
								aria-label="Password"
								type='password'
								value={password}
								onChange={e => setPassword(e.target.value)}
							/>
						</InputGroup>
						<Form.Control type="submit" style={{display: 'none'}}/>
					</Form>
				</Modal.Body>
				<Modal.Footer>
					<Button variant="primary" onClick={handleSend} disabled={!username || !password}>{isRegistering ? "Register and Sign in" : "Sign in"}</Button>
				</Modal.Footer>
			</Modal>
			<Modal show={assumeModalOpen} onHide={() => setAssumeModal(false)}>
				<Modal.Header closeButton>
					<Modal.Title>Assume as User</Modal.Title>
				</Modal.Header>
				<Modal.Body>
					<Form onSubmit={e => { handleSend(); e.preventDefault()}}>
						<InputGroup className="mb-3">
							<Form.Control
								placeholder="Assume Target"
								aria-label="Assume Target"
								value={assumeText}
								onChange={e => setAssumeText(e.target.value)}
							/>
						</InputGroup>
					</Form>
				</Modal.Body>
				<Modal.Footer>
					<Button variant="primary" onClick={() => {apiClient.assumeUser(assumeText); setAssumeModal(false);}} disabled={!assumeText}>Confirm</Button>
				</Modal.Footer>
			</Modal>
		</div>
	);
}

export default MainNav;