import axios, { CreateAxiosDefaults, AxiosInstance } from 'axios'
import config from './apiConfig.json'

if (config.disableTLSCheck) {
	process.env.NODE_TLS_REJECT_UNAUTHORIZED = "0"
}

export type Session = { 
	token: string, 
	expireAt: number, 
	type: number, 
	username: string 
}

export type ChatLine = {
	Thread: number,
	Result: {
		Lines: string[],
		PlayerOptions: string[],
		Result?: number,
	}
}

export type QueryResult = {
	Json: any,
	Table: any[]
	Field: any
}

export type Assistant = {
	id?: string,
	created_at: number,
	name: string,
	description: string,
	model: string,
	instructions: string,
	temperature: number,
	top_p: number,
	response_format?: object
	response_format_str?: string
}

export type PlayerData = {
    magin: number,
    currentSceneIdx: number,
    sceneUnlockDate: number,
    variables: {
        bools: Record<string, boolean>,
        strings: Record<string, string>,
        floats: Record<string, number>
    }
}

export class ApiClient {
	private api: AxiosInstance
	public session: Session | undefined
	public assumedAs: string | undefined

	private updateTrigger: () => void

	constructor(updateTrigger: () => void) {
		const axiosConfig: CreateAxiosDefaults = {
			baseURL: config.endpoint
		}

		this.updateTrigger = updateTrigger

		this.api = axios.create(axiosConfig)

		const localSessionStr = localStorage.getItem('session')
		if (localSessionStr) {
			const localSession: Session = JSON.parse(localSessionStr)
			// if (localSession.ExpireAt <= Date.now()) {
				this.api.defaults.headers['Authorization'] = localSession.token as string
			// }
			this.session = localSession
		}
	}

	assumeUser(target: string) {
		this.assumedAs = target
		this.api.defaults.headers['as_user'] = target
		this.updateTrigger()
	}

	unassumeUser() {
		this.assumedAs = undefined
		delete this.api.defaults.headers['as_user']
		this.updateTrigger()
	}

	async registerAndLogin(username: string, password: string) {
		await this.api.post(`/auth/register`, {
			username,
			password
		})
		await this.login(username, password)
	}

	async login(username: string, password: string) {
		const sessionObject = (await this.api.post(`/auth/login`, {
			username,
			password
		})).data

		sessionObject.UserName = username
		this.session = {
			...sessionObject,
			expireAt: (new Date(sessionObject.expireAt)).getTime(),
			username
		}
		localStorage.setItem('session', JSON.stringify(sessionObject))
		this.api.defaults.headers['Authorization'] = sessionObject.Token as string
		this.updateTrigger()
	}

	signOut() {
		this.session = undefined
		delete this.api.defaults.headers['Authorization']
		localStorage.removeItem('session')
		this.unassumeUser()
		this.updateTrigger()
	}

	checkToken() {
		if (!this.session) {
			throw new Error('Not Logged in')
		}

		if (this.session.expireAt <= Date.now()) {
			this.signOut()
			throw new Error('Expired Session. Please log in again')
		}
	}

	async getData() {
		const data =  (await this.api.get('/player/login_package')).data
		return {
			...data,
			sceneUnlockDate: (new Date(data.sceneUnlockDate)).getTime(),
		} as PlayerData
	}

	async startChat(assistantId: string, initialMessage: string): Promise<ChatLine> {
		this.checkToken()

		return (await this.api.post('/chat/start_chat', {
			PromptKey: assistantId,
			InitialMessage: initialMessage,
			SceneId: 'web-client'
		})).data
	}

	async continueChat (threadId: number, message: string): Promise<ChatLine> {
		this.checkToken()

		return (await this.api.post('/chat/chat', {
			Thread: threadId,
			Message: message
		})).data
	}

	async query(query: string) {
		return (await this.api.post('/admin/query', {
			Query: query
		})).data
	}

	async listModels () {
		return (await this.api.get('chat/models')).data.sort()
	}

	async listAsst (before?: string, after?: string) {
		const result = (await this.api.get('/chat/assts', {
			headers: {
				before,
				after
			}
		})).data as { data: Assistant[], first_id: string, last_id: string, has_more: boolean }

		for (const entry of result.data) {
			entry.response_format_str = JSON.stringify(entry.response_format, null, 4)
		}

		return result
	}

	async getAsst(asstId: string) {
		const result = (await this.api.get(`/chat/assts/${asstId}`)).data as Assistant
		result.response_format_str = JSON.stringify(result.response_format, null, 4)
		return result
	}

	async delAsst(asstId: string) {
		return (await this.api.delete(`/chat/assts/${asstId}`)).data
	}

	async createAsst(asst: Assistant) {
		asst.response_format = asst.response_format_str ? JSON.parse(asst.response_format_str) : undefined
		return (await this.api.post(`/chat/assts/`, asst)).data as Assistant
	}

	async updateAsst(asstId: string, asst: Assistant) {
		asst.response_format = asst.response_format_str ? JSON.parse(asst.response_format_str) : undefined
		return (await this.api.put(`/chat/assts/${asstId}`, asst)).data as Assistant
	}
}