import { useCallback, useEffect, useLayoutEffect, useRef, useState } from 'react'
import Form from 'react-bootstrap/Form'
import './Trainer.css'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import ListGroup from 'react-bootstrap/ListGroup';
import Button from 'react-bootstrap/Button'
import Modal from 'react-bootstrap/Modal'
import Alert from 'react-bootstrap/Alert'
import MemoryProviders, { CompileScenario, ConversationBlock, IMemoryProvider, ProviderConfiguration, Scenario, SerializeScenario } from './memory/MemoryProviders'
import { ButtonGroup, Pagination } from 'react-bootstrap'
import promptsRaw from './memory/prompts.json'
import { exportFile, FileDialog } from './util/fileIo'

const prompts = promptsRaw as Record<string, Scenario>

type WorkingSetElement = {
	scenarioId: string,
	conversation: ConversationBlock[]
}

function ProviderConfigLine({ id, config, value, setter }: { id: string, config: ProviderConfiguration, value: any, setter: (id: string, value: any) => void }) {
	let inner: React.ReactNode
	switch (config.type) {
		case 'text':
		case 'password':
			inner = <Form.Control type={config.type} value={value ?? ''} onChange={e => setter(id, e.target.value)} />
			break
		case 'number':
			inner = <Form.Control type='number' min={config.min} max={config.max} value={value ?? 0} onChange={e => setter(id, e.target.value)} />
			break
		case 'range':
			inner = <Row>
				<Col md={2}>
					<Form.Label>{value}</Form.Label>
				</Col>
				<Col md={10}>
					<Form.Range min={config.min} max={config.max} step={config.step} value={value ?? 0} onChange={e => setter(id, parseFloat(e.target.value))} />
				</Col>
			</Row>
			break
		case 'select':
			inner = <Form.Select value={value ?? ''} onChange={e => setter(id, e.target.value)}>
				{config.options && config.options.map(k => <option key={k}>{k}</option>)}
			</Form.Select>
			break
	}

	return <Form.Group controlId={id} className="formBlock">
		<Form.Label>{id}</Form.Label>
		{inner}
		{config.description ? <Form.Text muted>{config.description}</Form.Text> : null}
	</Form.Group>
}

function ScenarioSettingLine({ id, title, value, setter, disabled, inline, textarea }: {
	id: string,
	title: string,
	value?: Scenario,
	setter: (reducer: (s: Scenario | undefined) => Scenario | undefined) => void,
	disabled?: boolean,
	inline?: boolean,
	textarea?: number
}) {
	// let { responses: _, ...rest } = value ?? {};
	const dict = value as Record<string, string> ?? {}

	const onChange = useCallback((e: any) => {
		setter(s => s ? { ...s, [id]: e.target.value } : undefined)
	}, [id, setter])

	if (inline) {
		return <Form.Group controlId={title} as={Row} className="formBlock">
			<Form.Label column sm={3}>{title}</Form.Label>
			<Col sm={9}>
				<Form.Control value={dict[id] ?? ''} onChange={onChange} disabled={disabled || !value} />
			</Col>
		</Form.Group>
	}

	return <Form.Group controlId={title} className="formBlock">
		<Form.Label>{title}</Form.Label>
		{
			<Form.Control
				as={textarea ? 'textarea' : undefined}
				rows={textarea} value={dict[id] ?? ''}
				onChange={onChange}
				disabled={disabled || !value}
			/>
		}
	</Form.Group>
}

function ConvoLine({ value, index, action, onContentChange, onRegenerate, onAction, readOnly }: {
	value: ConversationBlock,
	onContentChange: (newContent: ConversationBlock) => void,
	index: number,
	action?: string,
	onRegenerate?: (index: number) => void,
	onAction?: (index: number) => void,
	readOnly?: boolean
}) {
	const textareaRef = useRef<HTMLTextAreaElement | null>(null)

	useLayoutEffect(() => {
		if (!textareaRef.current) return
		// Reset height - important to shrink on delete
		textareaRef.current.style.height = "inherit";
		// Set height
		textareaRef.current.style.height = `${textareaRef.current.scrollHeight}px`;
	}, [value.content]);

	return <ListGroup.Item className='convoLineContainer'>
		<div className='convoHeader'>
			<div className='convoHeaderLeft'>
				<span className='convoSpeaker'>{value.role}</span>
				{value.meta ? <span className='convoLineMeta'>{value.meta.contextTokens} context tokens, {value.meta.completionTokens} completion tokens, {value.meta.delay.toFixed(3)}s</span> : null}
				{value.role === 'assistant' ? <span className='convoLineRefresh' onClick={() => { if (onRegenerate) onRegenerate(index) }}>&#10227;</span> : null}
			</div>
			{action ? <div className='convoHeaderDrop' onClick={() => { if (onAction) onAction(index) }}>{action}</div> : null}
		</div>
		<Form.Control
			ref={textareaRef}
			as='textarea'
			className='convoLine'
			onChange={e => onContentChange({ ...value, content: e.target.value })}
			value={value.content}
			readOnly={readOnly}
		/>
	</ListGroup.Item>
}

function NewConvoDialog({ scenarioId, setScenarioId, commit }: { scenarioId?: string, setScenarioId: (s: string | undefined) => void, commit: () => void }) {
	const [error, setError] = useState<string | undefined>()
	const hide = useCallback(() => setScenarioId(undefined), [setScenarioId])
	return <Modal show={Boolean(scenarioId)} onHide={hide}>
		<Modal.Header closeButton>
			<Modal.Title>Select a Scenario for Conversation</Modal.Title>
		</Modal.Header>

		<Modal.Body>
			{error ? <Alert variant='danger' onClose={() => setError(undefined)}>{error}</Alert> : null}
			<Form.Group controlId="formSelect" className="mb-3">
				<Form.Select value={scenarioId} onChange={e => setScenarioId(e.target.value)}>
					{Object.keys(prompts).map(s => <option key={s}>{s}</option>)}
				</Form.Select>
			</Form.Group>
		</Modal.Body>

		<Modal.Footer>
			<Button variant="secondary" onClick={hide}>Close</Button>
			<Button variant="primary" onClick={commit}>Add</Button>
		</Modal.Footer>
	</Modal>
}

function JumpDialog({ show, onCancel, onCommit, target, setTarget, min, max }: {
	show: boolean,
	onCancel: () => void,
	onCommit: () => void,
	target: string,
	setTarget: (page: string) => void,
	min: number,
	max: number
}) {
	return <Modal show={show} onHide={onCancel}>
		<Modal.Header closeButton>
			<Modal.Title>Jump to Page...</Modal.Title>
		</Modal.Header>

		<Modal.Body>
			<Form.Group controlId="formSelect" className="mb-3">
				<Form.Control type='number' min={min} max={max} value={target} onChange={e => setTarget(e.target.value)} />
				<Form.Text muted>{`Max ${max} pages`}</Form.Text>
			</Form.Group>
		</Modal.Body>

		<Modal.Footer>
			<Button variant="secondary" onClick={onCancel}>Close</Button>
			<Button variant="primary" onClick={onCommit}>Go</Button>
		</Modal.Footer>
	</Modal>
}

function CopyDialog({text, onClose}: {text?: string, onClose: () => void}) {
	return <Modal show={Boolean(text)} onHide={onClose}>
		<Modal.Header closeButton>
			<Modal.Title>Copy Scenario</Modal.Title>
		</Modal.Header>

		<Modal.Body>
			<p className='copyText'>{text}</p>
		</Modal.Body>

		<Modal.Footer>
			<Button variant="secondary" onClick={onClose}>Close</Button>
		</Modal.Footer>
	</Modal>
}

function Trainer() {
	const [[providerKey, provider], setProviderPair] = useState<[string, IMemoryProvider]>(() => {
		const defaultProvider = localStorage.getItem('defaultProvider') ?? 'none'
		return [defaultProvider, MemoryProviders[defaultProvider]()]
	})
	const [activeConfig, setActiveConfig] = useState<Record<string, any>>({})
	const [workingSet, setWorkingSet] = useState<WorkingSetElement[]>([])
	const [workingSetId, setWorkingSetId] = useState(-1)
	const [scenario, setScenario] = useState<Scenario | undefined>(undefined)
	const [convo, setConvo] = useState<ConversationBlock[]>([])
	const [lock, setLock] = useState(false)

	// Dialog States
	const [newDialogScenario, setNewDialogScenario] = useState<string | undefined>(undefined)
	const [showFileDialog, setShowFileDialog] = useState<boolean>(false)
	const [showJumpDialog, setShowJumpDialog] = useState<boolean>(false)
	const [jumpTarget, setJumpTarget] = useState<string>('0')
	const [copyText, setCopytext] = useState<string | undefined>(undefined)

	// Sync settings with selected provider
	useEffect(() => {
		const settingsManifest = provider.renderControls()
		const inititalSettings: Record<string, any> = {}

		for (const [setting, config] of Object.entries(settingsManifest)) {
			const stored = localStorage.getItem(`${providerKey}_${setting}`)
			inititalSettings[setting] = stored ? JSON.parse(stored) : config.default
		}

		setActiveConfig(inititalSettings)
	}, [providerKey, provider])

	// handle that stupid copy button
	const handleCopy = useCallback(() => {
		if (!scenario) {
			console.log('no scenario')
			return
		}

		const scenarioStr = SerializeScenario(scenario)

		if (navigator.clipboard) {
			navigator.clipboard.writeText(scenarioStr)
		}
		else {
			console.log(scenarioStr)
			setCopytext(scenarioStr)
		}
	}, [scenario])

	// NavigationCallbacks
	const handleClear = useCallback(() => {
		setWorkingSet([])
		setWorkingSetId(-1)
		setScenario(undefined)
		setConvo([])
	}, [])

	const handleImport = useCallback((file: any) => {
		const newWs = file as WorkingSetElement[]

		if (newWs.length === 0) {
			return
		}

		setWorkingSetId(workingSet.length)
		setWorkingSet(ws => [...ws, ...newWs])
		setScenario(prompts[newWs[0].scenarioId])
		setConvo(newWs[0].conversation)

		setShowFileDialog(false)
	}, [workingSet.length])

	const saveConvoToWorkingSet = useCallback(() => {
		if (workingSetId < 0) {
			return
		}
		setWorkingSet(ws => {
			const newWorkingSet = [...ws]
			newWorkingSet[workingSetId].conversation = convo
			return newWorkingSet
		})
	}, [convo, workingSetId])

	const navigateTo = useCallback((index: number) => {
		// Save state
		saveConvoToWorkingSet()

		// Load new convo
		setWorkingSetId(index)
		const { scenarioId, conversation } = workingSet[index]
		setScenario(prompts[scenarioId])
		setConvo(conversation)
	}, [saveConvoToWorkingSet, workingSet])

	const insertConvoAfter = useCallback((index: number, scenarioId: string, conversation: ConversationBlock[] = []) => {
		saveConvoToWorkingSet()
		setWorkingSet(ws => {
			const newWorkingSet = [...ws]
			newWorkingSet.splice(index + 1, 0, {
				scenarioId: scenarioId,
				conversation
			})
			return newWorkingSet
		})
		setWorkingSetId(index + 1)
		setScenario(prompts[scenarioId])
		setConvo(conversation)
	}, [saveConvoToWorkingSet])

	const handleDuplicate = useCallback(() => {
		if (!scenario) {
			return
		}
		insertConvoAfter(workingSetId, scenario.id, convo)
	}, [insertConvoAfter, scenario, workingSetId, convo])

	const handleReset = useCallback(() => {
		const current = workingSet[workingSetId]
		setConvo(current.conversation)
		setScenario(prompts[current.scenarioId])
	}, [workingSet, workingSetId])

	const handleNewConvo = useCallback(() => {
		if (!newDialogScenario) {
			return
		}
		insertConvoAfter(workingSetId, newDialogScenario)
		setNewDialogScenario(undefined) // close the dialog
	}, [insertConvoAfter, newDialogScenario, workingSetId])

	const handleDelete = useCallback(() => {
		const newWorkingSet = [...workingSet]
		newWorkingSet.splice(workingSetId, 1)
		setWorkingSet(newWorkingSet)

		const isCleared = newWorkingSet.length === 0
		const newIndex = isCleared ? -1 : Math.max(0, workingSetId - 1)
		setWorkingSetId(newIndex)

		setScenario(isCleared ? undefined : prompts[newWorkingSet[newIndex].scenarioId])
		setConvo(isCleared ? [] : newWorkingSet[newIndex].conversation)
	}, [workingSet, workingSetId])

	// Setting Handlers
	const handleConfigSet = useCallback((id: string, value: any) => {
		setActiveConfig(config => ({ ...config, [id]: value }))
	}, [])


	// Conversation Module Handlers
	const curryConvoSetter = useCallback((index: number) => {
		return (conversation: ConversationBlock): void => {
			setConvo(c => {
				const newConvo = [...c]
				newConvo[index] = conversation
				return newConvo
			})
		}
	}, [])

	const handleGenerate = useCallback(async (index: number) => {
		if (!scenario) {
			return
		}

		const compiled: ConversationBlock[] = [
			{ role: 'system', content: CompileScenario(scenario) },
			{ role: 'assistant', content: scenario.lines },
			...convo.slice(0, index)
		]

		const onUpdate = curryConvoSetter(index)

		setLock(true)
		try {
			await provider.send(activeConfig, compiled, onUpdate, scenario.player_name)
		}
		catch (e: any) {
			onUpdate({ role: 'assistant', content: e.message ?? JSON.stringify(e) })
		}
		finally {
			setLock(false)
		}
	}, [activeConfig, provider, convo, curryConvoSetter, scenario])

	const handleNewConvoBlock = useCallback(() => {
		const isAsst = convo.length % 2 === 1
		setConvo(c => [...c, isAsst ? { role: 'assistant', content: '' } : { role: 'user', content: `${scenario?.player_name}: ` }])

		if (isAsst) {
			handleGenerate(convo.length)
		}
	}, [handleGenerate, convo, scenario])

	const handleTruncate = useCallback((i: number) => {
		setConvo(c => c.slice(0, i))
	}, [])

	const handleSaveSetting = useCallback(() => {
		localStorage.setItem('defaultProvider', providerKey)
		for (const [configKey, configValue] of Object.entries(activeConfig)) {
			localStorage.setItem(`${providerKey}_${configKey}`, JSON.stringify(configValue))
		}
	}, [activeConfig, providerKey])

	return (
		<div className='trainPage'>
			<Row>
				<Col xs={12} md={12} lg={4}>
					<div className="block">
						<div className='scenarioHeaderRow'>
							<h5 className="header">Scenario</h5>
							<div className='scenarioCopyButton' onClick={handleCopy}>Copy</div>
						</div>
						<Form>
							<Form.Text>Any changes made here is not saved.</Form.Text>
							<ScenarioSettingLine title='ID' id='id' value={scenario} setter={setScenario} disabled inline />
							{/* <ScenarioSettingLine title='Player Name' id='player_name' value={scenario} setter={setScenario} disabled={lock} inline /> */}
							<ScenarioSettingLine title='Major Characters' id='major_characters' value={scenario} setter={setScenario} disabled={lock} inline />
							<ScenarioSettingLine title='Minor Characters' id='minor_characters' value={scenario} setter={setScenario} disabled={lock} inline />
							<hr />
							<ScenarioSettingLine title='Context' id='context' value={scenario} setter={setScenario} disabled={lock} textarea={10} />
							<ScenarioSettingLine title='Objective' id='objective' value={scenario} setter={setScenario} disabled={lock} textarea={3} />
							<ScenarioSettingLine title='Directives' id='directives' value={scenario} setter={setScenario} disabled={lock} textarea={5} />
						</Form>
					</div>
				</Col>
				<Col xs={12} sm={8} md={8} lg={5} xl={6} className='mainDivContainer'>
					<div className="block mainDiv">
						<h5 className="header">Conversation</h5>
						<ListGroup>
							{scenario ? <ConvoLine
								key='prompt'
								value={{ role: 'system', content: scenario.lines }}
								index={-1}
								onContentChange={ct => setScenario(s => (s ? { ...s, lines: ct.content } : undefined))}
								readOnly={lock}
							/> : null}
							{convo.map((c, i) => <ConvoLine
								key={i}
								value={c}
								action='Truncate'
								onRegenerate={handleGenerate}
								onAction={handleTruncate}
								onContentChange={curryConvoSetter(i)}
								index={i}
								readOnly={lock}
							/>)}
						</ListGroup>
						<br />
						{scenario ? <Row><Button variant='outline-primary' onClick={handleNewConvoBlock} disabled={lock}>New Block</Button></Row> : null}
					</div>
				</Col>
				<Col xs={12} sm={4} md={4} lg={3} xl={2}>
					<div className="block">
						<h5 className="header">Dataset IO</h5>
						<Row>
							<ButtonGroup>
								<Button variant="primary" onClick={() => setShowFileDialog(true)}>Import</Button>
								<Button variant="secondary" onClick={() => exportFile(workingSet, "dataset")}>Export</Button>
								<Button variant="danger" onClick={handleClear}>Clear All</Button>
							</ButtonGroup>
						</Row>
					</div>
					<div className="block">
						<h5 className="header">Navigation</h5>
						<Row className="navBlock">
							<ButtonGroup>
								<Button variant="primary" onClick={() => setNewDialogScenario(Object.keys(prompts)[0])}>New</Button>
								<Button variant="secondary" onClick={handleDuplicate} disabled={workingSetId < 0}>Dupe</Button>
								<Button variant="danger" onClick={handleReset} disabled={workingSetId < 0}>Reset</Button>
								<Button variant="danger" onClick={handleDelete} disabled={workingSetId < 0}>Delete</Button>
							</ButtonGroup>
						</Row>
						<hr />
						<Row className="navBlock">
							<ButtonGroup>
								<Button variant="outline-primary" onClick={() => navigateTo(0)} disabled={workingSetId <= 0}>{'1'}</Button>
								<Button variant="outline-primary" onClick={() => navigateTo(workingSetId - 1)} disabled={workingSetId <= 0}>{'<'}</Button>
								<Button variant="primary" onClick={() => { setShowJumpDialog(true); setJumpTarget((workingSetId + 1).toString()); }} disabled={workingSetId < 0}>{workingSetId + 1}</Button>
								<Button variant="outline-primary" onClick={() => navigateTo(workingSetId + 1)} disabled={workingSetId < 0 || workingSetId >= workingSet.length - 1}>{'>'}</Button>
								<Button variant="outline-primary" onClick={() => navigateTo(workingSet.length - 1)} disabled={workingSetId < 0 || workingSetId >= workingSet.length - 1}>{workingSet.length || 1}</Button>
							</ButtonGroup>
						</Row>
						<Pagination></Pagination>
					</div>
					<div className="block">
						<h5 className="header">Inference Settings</h5>
						<Form>
							<Form.Group controlId="providerType" className="formBlock">
								<Form.Label>Provider</Form.Label>
								<Form.Select value={providerKey} onChange={e => setProviderPair([e.target.value, MemoryProviders[e.target.value]()])}>
									{Object.keys(MemoryProviders).map(k => <option key={k}>{k}</option>)}
								</Form.Select>
							</Form.Group>
							<hr />
							{Object.entries(provider.renderControls()).map(([k, v]) => <ProviderConfigLine key={k} id={k} config={v} setter={handleConfigSet} value={activeConfig[k]} />)}
							<Row><Button variant="secondary" onClick={handleSaveSetting}>Save Settings to localStorage</Button></Row>
						</Form>
					</div>
				</Col>
			</Row>
			<NewConvoDialog scenarioId={newDialogScenario} setScenarioId={setNewDialogScenario} commit={handleNewConvo} />
			<FileDialog show={showFileDialog} onCancel={() => setShowFileDialog(false)} onFileUpload={handleImport} />
		    <CopyDialog text={copyText} onClose={() => setCopytext(undefined)}/>
			<JumpDialog
				show={showJumpDialog}
				onCancel={() => setShowJumpDialog(false)}
				min={1}
				max={workingSet.length}
				target={jumpTarget}
				setTarget={setJumpTarget}
				onCommit={() => { const jt = parseInt(jumpTarget); if (!isNaN(jt) && jt > 0 && jt <= workingSet.length) { navigateTo(jt - 1); setShowJumpDialog(false); } }}
			/>
		</div>
	)
}

export default Trainer