import { ConversationBlock, IMemoryProvider, ProviderConfiguration } from './MemoryProviders'
import axios from 'axios'

export default class LocalOllamaProvider implements IMemoryProvider {

	constructor() {
	}

	async init() {

	}

	async send(setting: Record<string, any>, history: ConversationBlock[], onUpdate: (conversation: ConversationBlock) => void, playerName: string): Promise<void> {
		try {
			history = [...history]
			const system = history.shift()?.content

			for (const index in history) {
				if (history[index].meta) {
					delete history[index].meta
				}
			}

			const start = Date.now()
			const completion = (await axios.post(setting.Endpoint, {
				model: setting.Model,
				messages: history,
				system,
				options: {
					temperature: setting.Temperature || 1
				},
				stream: false
			})).data

			let target = ''
			
			onUpdate({
				role: 'assistant',
				content: target,
				meta: {
					completionTokens: completion.eval_count - completion.prompt_eval_count,
					contextTokens: completion.prompt_eval_count,
					delay: (Date.now() - start) / 1000
				}
			})
		}
		catch (e) {
			console.log('error', e)
			throw e
		}
	}

	renderControls(): Record<string, ProviderConfiguration> {
		return {
			Endpoint: {
				type: 'text',
				default: 'http://localhost:11434/api/generate',
				// description: 'This is never saved for security reasons.'
			},
			Model: {
				type: 'text',
				default: ''
			},
			Temperature: {
				type: 'range',
				default: 1,
				min: 0,
				max: 2,
				step: 0.1
			}
		}
	}
}