import { useContext, useState } from 'react'
import Form from 'react-bootstrap/Form'
import './Chat.css'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import ListGroup from 'react-bootstrap/ListGroup';
import { ChatLine } from './util/ApiClient'
import { ApiContext } from './util/contexts'
import InputGroup from 'react-bootstrap/InputGroup'
import Button from 'react-bootstrap/Button'

type DisplayLine = {
	speaker: 'remote' | 'user', 
	content: string,
	isError?: boolean
}

function Chat () {
	const [userInput, setUserInput] = useState('')
	const [msgList, setMsgList] = useState<DisplayLine[]>( [])
	const [targetAssistant, setTargetAssistant] = useState('')
	const [activeOptions, setActiveOptions] = useState<string[] | null>(null)
	const [activeThreadId, setActiveThreadId] = useState<number | null>(null)
	const [statusMessage, setStatusMessage] = useState('Ready')
	const [lockInput, setLockInput] = useState(false)

	const apiClient = useContext(ApiContext)

	const reset = () => {
		setUserInput('')
		setMsgList([])
		setActiveOptions(null)
		setActiveThreadId(null)
		setStatusMessage('Ready')
		setLockInput(false)
	}

	const handleSend = async (input: string) => {
		let line: ChatLine

		setMsgList(msgList => msgList.concat({ speaker: 'user', content: input }))
		setUserInput('')
		setStatusMessage('Requesting')
		setActiveOptions(null)
		setLockInput(true)

		const startTime = Date.now()
		
		try {
			if (activeThreadId) {
				line = await apiClient.continueChat(activeThreadId, input)
			}
			else {
				line = await apiClient.startChat(targetAssistant, input)
			}

			if (!line.Result) {
				throw new Error('ChatGPT Encountered an Error. See Server Details')
			}
		}
		catch (e: any) {
			setStatusMessage(`Error Encountered after ${Date.now() - startTime} ms`)
			setMsgList(list => [...list, { speaker: 'remote', content: e.response?.data ?? e.message, isError: true } as DisplayLine])
			setLockInput(false)
			return
		}

		setStatusMessage(`Response received after ${Date.now() - startTime} ms`)
		setActiveThreadId(line.Thread)
		setMsgList(msgList => msgList.concat(line.Result.Lines.map(l => ({ speaker: 'remote', content: l }))))
		
		if (line.Result.Result !== null) {
			setMsgList(msgList => [...msgList, {speaker: 'remote', content: `Chat ended with result ${line.Result.Result}`}])
		}
		else {
			setActiveOptions(line.Result.PlayerOptions)
			setLockInput(false)
		}
	}


	return (
		<div className='chatPage'>
			<div className='asstInput'>
				<InputGroup>
					<InputGroup.Text id="asst-addon">Assistant</InputGroup.Text>
					<Form.Control
						placeholder="Enter Assistant ID"
						aria-label="Assistant ID"
						value={targetAssistant}
						disabled={!!activeThreadId}
						onChange={e => setTargetAssistant(e.target.value)}
					/>
				</InputGroup>
			</div>
			<Row className="threadBlock">
				<Col className='col-10'>
					<InputGroup>
						<InputGroup.Text id="asst-addon">Thread</InputGroup.Text>
						<Form.Control
							placeholder="Send a message to create a thread"
							aria-label="Thread ID"
							value={activeThreadId ?? ''}
							disabled={true}
						/>
					</InputGroup>
				</Col>
				<Button className='col-2' variant='outline-danger' onClick={reset}>Reset</Button>
			</Row>
			<div className='msgDisplay'>
				{msgList.map((msg, i) => <p key={i} className={msg.isError ? 'line error' : 'line'}>{`${msg.speaker}: ${msg.content}`}</p>)}
			</div>
			{activeOptions ? <ListGroup className='optionsContainer'>
				{activeOptions.map((o, i) => <ListGroup.Item action key={i} onClick={() => handleSend(o)}>{o}</ListGroup.Item>)}
			</ListGroup> : null}
			<div className='chatStatus'>
				{statusMessage}
			</div>
			<Form className='inputForm'>
				<Row className='inputRow'>
					<Col className='col-9'>
						<Form.Control
							placeholder="Send Messages here..."
							aria-label="Input"
							as="textarea"
							value={userInput}
							disabled={lockInput || !targetAssistant}
							onChange={e => setUserInput(e.target.value)}
							onKeyUp={e => {
								if(e.key === 'Enter' && !e.shiftKey) {
									e.preventDefault();
									handleSend(userInput)
								}
							}}
						/>
					</Col>
					<Col className='col-3'>
						<Form.Control type="submit" onClick={() => handleSend(userInput)} disabled={lockInput || !targetAssistant}/>
					</Col>
				</Row>
			</Form>
		</div>
	)
}

export default Chat