import Groq from 'groq-sdk'
import { ConversationBlock, IMemoryProvider, ProviderConfiguration } from './MemoryProviders'
import { parseIntWithdefault as parseIntWithDefault } from '../util/parseIntUtil'

export default class GroqProvider implements IMemoryProvider {
	private api: Groq
	private lastToken: string = ''

	constructor() {
		this.api = new Groq({
			apiKey: '',
			dangerouslyAllowBrowser: true
		})
	}

	async init() {

	}

	async send(setting: Record<string, any>, history: ConversationBlock[], onUpdate: (conversation: ConversationBlock) => void, playerName: string): Promise<void> {
		try {
			if (this.lastToken !== setting.Token) {
				this.lastToken = setting.Token
				this.api = new Groq({
					apiKey: setting.Token,
					dangerouslyAllowBrowser: true
				})
			}

			history = [...history]

			for (const index in history) {
				if (history[index].meta) {
					delete history[index].meta
				}
			}

			const start = Date.now()
			const chatCompletion = await this.api.chat.completions.create({
				'messages': history,
				'model': setting.Model,
				'temperature': setting.Temperature || 1,
				'max_completion_tokens': parseIntWithDefault(setting['Max Tokens'], 512),
				'top_p': 1,
				'stream': true,
				'stop': `${playerName}: `
			})

			let target = ''
			
			for await (const chunk of chatCompletion) {
				target += chunk.choices[0]?.delta?.content || ''
				const usage = chunk.x_groq?.usage
				onUpdate({
					role: 'assistant',
					content: target,
					meta: usage ? {
						completionTokens: usage.total_tokens - usage.prompt_tokens,
						contextTokens: usage.prompt_tokens,
						delay: (Date.now() - start) / 1000
					} : undefined
				})
			}
		}
		catch (e) {
			console.log('error', e)
			console.log(e instanceof Groq.APIError)
			if (e instanceof Groq.APIError) {
				throw new Error(`${e.error.error.code}: ${e.error.error.message}`)
			}
			else {
				throw e
			}
		}
	}

	renderControls(): Record<string, ProviderConfiguration> {
		return {
			Token: {
				type: 'text',
				default: '',
				// description: 'This is never saved for security reasons.'
			},
			Model: {
				type: 'select',
				default: 'mistral-saba-24b',
				options: [
					'mistral-saba-24b', 
					'gemma2-9b-it',
					'llama-3.1-8b-instant', 
					'llama-3.3-70b-versatile'
				]
			},
			'Max Tokens': {
				type: 'number',
				default: 512,
				min: 0,
				max: 4096
			},
			Temperature: {
				type: 'range',
				default: 1,
				min: 0,
				max: 2,
				step: 0.1
			}
		}
	}
}