import { useCallback, useRef, useState } from 'react'
import Alert from 'react-bootstrap/Alert'
import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button'
import Modal from 'react-bootstrap/Modal'

export function FileDialog({ show, onFileUpload, onCancel }: { show: boolean, onFileUpload: (file: any) => void, onCancel: () => void }) {
	const [error, setError] = useState<string | null>(null)
	const fileUploadRef = useRef<HTMLInputElement | null>(null)
	const onSubmit = useCallback(async () => {
		// Ignore if < 0 files
		if (!fileUploadRef.current?.files?.length) {
			return
		}

		const file = await fileUploadRef.current.files[0].text()
		const obj = JSON.parse(file)
		if (!Array.isArray(obj)) {
			return
		}
		onFileUpload(JSON.parse(file))
	}, [onFileUpload])

	return <Modal show={show} onHide={onCancel}>
		<Modal.Header closeButton>
			<Modal.Title>Import File...</Modal.Title>
		</Modal.Header>

		<Modal.Body>
			{error ? <Alert variant='danger' onClose={() => setError(null)}>{error}</Alert> : null}
			<Form.Group controlId="formFile" className="mb-3">
				<Form.Control type="file" ref={(ref: HTMLInputElement) => { fileUploadRef.current = ref }} />
			</Form.Group>
		</Modal.Body>

		<Modal.Footer>
			<Button variant="secondary" onClick={onCancel}>Close</Button>
			<Button variant="primary" onClick={onSubmit}>Import</Button>
		</Modal.Footer>
	</Modal>
}

export function exportFile(content: any, fileName: string) {
	const json = JSON.stringify(content, null, 2);
	const blob = new Blob([json], { type: "application/json" });
	const href = URL.createObjectURL(blob);

	// create "a" HTLM element with href to file
	const link = document.createElement("a");
	link.href = href;
	link.download = fileName + ".json";
	document.body.appendChild(link);
	link.click();

	// clean up "a" element & remove ObjectURL
	document.body.removeChild(link);
	URL.revokeObjectURL(href);
}