import DummyProvider from "./DummyProvider"
import GroqProvider from "./GroqProvider"
import GeminiProvider from "./GeminiProvider"
import DeepseekProvider from './DeepseekProvider'
import LocalOllamaProvider  from './LocalOllamaProvider'
import additions from './addendums.json'

export interface IMemoryProvider {
	init(): Promise<void>
	send(setting: Record<string, any>, history: ConversationBlock[], onUpdate: (conversation: ConversationBlock) => void, playerName: string): Promise<void>
	renderControls(): Record<string, ProviderConfiguration>
}

export type ProviderConfiguration = {
	type: 'text' | 'password' | 'number' | 'range' | 'select'
	default: any
	min?: number
	max?: number
	step?: number
	options?: string[]
	description?: string
	no_save?: boolean
}

export type ConversationBlock = {
	role: 'system' | 'user' | 'assistant',
	content: string
	meta?: {
		contextTokens: number,
		completionTokens: number,
		delay: number
	}
}

export type Scenario = {
	id: string,
	player_name: string,
	major_characters: string,
	minor_characters: string,
	context: string,
	objective: string,
	directives: string,
	lines: string
}

export function CompileScenario (scenario: Scenario): string {
	return [additions.base, scenario.context, scenario.objective, scenario.directives].join('\n\n')
}

export function SerializeScenario (scenario: Scenario): string {
	return Object.entries(scenario).map(([key, value]) => `[[${key}]]\r\n${typeof value === 'string' ?  value : (value as unknown as string[]).join('\r\n')}\r\n`).join('\r\n')
}

const factories: Record<string, () => IMemoryProvider> = {
	'none': () => new DummyProvider(),
	'groq': () => new GroqProvider(),
	'gemini': () => new GeminiProvider(),
	'deepseek': () => new DeepseekProvider(),
	'local_ollama': () => new LocalOllamaProvider()
}

export default factories