import React, { ReactElement, useRef, useState } from 'react';
import MainNav from './MainNav';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import { ApiContext } from './util/contexts';
import { ApiClient } from './util/ApiClient';
import Chat from './Chat';
import QueryPage from './Query';
import PlayerPage from './PlayerData';
import Trainer from './Trainer'

function App() {
	const [updateTrigger, setUpdateTrigger] = useState(0)
	const [tab, setTab] = useState('home')
	const api = useRef(new ApiClient(() => setUpdateTrigger(t => t + 1)))

	let activeModule: ReactElement;
	switch(tab) {
		case 'home': 
			activeModule = <p>{api.current.session ? `Hello ${api.current.session.username}` : 'Please Sign in!'}</p>	
			break
		case 'data':
			activeModule= <PlayerPage/>
			break
		case 'chat':
			activeModule = <Chat/>
			break
		case 'query':
			activeModule = <QueryPage/>
			break
		case 'trainer':
			activeModule = <Trainer/>
			break
		default:
			activeModule = <p>{tab}</p>
			break
	}

	return (
		<div className="App" >
			<ApiContext.Provider value={api.current}>
				<MainNav trigger={updateTrigger} session={api.current.session} tab={tab} setTab={setTab} assumeAs={api.current.assumedAs}/>
				<div className="appBody">
					{activeModule}
				</div>
			</ApiContext.Provider>
		</div>
	);
}

export default App;
