import { ReactElement, useContext, useState } from "react"
import Form from 'react-bootstrap/Form'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Alert from 'react-bootstrap/Alert'
import Table from 'react-bootstrap/Table'
import Modal from 'react-bootstrap/Modal'
import { ApiContext } from "./util/contexts"
import "./Query.css"


function Cell ({ data, onInspect }: { data: any, onInspect: (str: string) => void }) {
	if (data == null) {
		return <td/>
	} 

	const str = data.toString()
	return <td className='inspectableCell' onClick={() => onInspect(data)}>
		<div>{str}</div>
	</td>
}

function QueryPage () {
	const [query, setQuery] = useState('')
	const [error, setError] = useState('')
	const [data, setData] = useState<any>(null)
	const [modalContent, setModalContent] = useState(null)
	const api = useContext(ApiContext)

	const handleSend = async (input: string) => {
		try {
			setData(await api.query(input))
			setError('')
		}
		catch (e: any) {
			setError(e.response?.data ?? e.message)
		}
	}
	
	const handleCellInspect = (data: any) => {
		setModalContent(data)
	}

	let output: ReactElement | null = null

	if (data) {
		if (data.Fields) {
			let rows = data.Result.map((row: any, i: number) => <tr key={i}>
				{data.Fields.map((f: string) => <Cell key={f} data={row[f]} onInspect={handleCellInspect}/>)}
			</tr>)

			// Generate table
			output = <Table striped bordered hover>
				<thead>
					<tr>
						{data.Fields.map((f: string) => <th key={f}>{f}</th>)}
					</tr>
				</thead>
				<tbody>
					{rows}
				</tbody>
			</Table>
		}
		else {	
			output = <Table striped bordered className="dataTable">
				<thead>
					<tr>
						<th>Key</th>
						<th>Value</th>
					</tr>
				</thead>
				<tbody>
					{Object.entries(data.Result as Record<string, string>).map(([key, value]) => <tr key={key}><td>{key}</td><td>{value}</td></tr>)}
				</tbody>
			</Table>
		}
	} 

	return (
		<div className='queryPage'>
			<Form className='inputForm'  onSubmit={e => { handleSend(query); e.preventDefault()}}>
				<Row className='inputRow'>
					<Col className='col-9'>
						<Form.Control
							placeholder="Send Messages here..."
							aria-label="Input"
							as="textarea"
							value={query}
							onChange={e => setQuery(e.target.value)}
							onKeyUp={e => {
								if(e.key === 'Enter' && !e.shiftKey) {
									e.preventDefault();
									handleSend(query)
								}
							}}
						/>
					</Col>
					<Col className='col-3'>
						<Form.Control type="submit"/>
					</Col>
				</Row>
			</Form>
			{error ? <Alert variant="danger">{error}</Alert> : null}
			{output}
			<Modal show={!!modalContent} onHide={() => setModalContent(null)}>
				<Modal.Header closeButton>
					<Modal.Title>Inspect Cell</Modal.Title>
				</Modal.Header>
				<Modal.Body>
					<p className="inspectData">{modalContent}</p>
				</Modal.Body>
			</Modal>
		</div>
	)
}

export default QueryPage