import { useContext, useState, useEffect, useCallback } from 'react'
import { ApiContext } from './util/contexts'
import { PlayerData } from './util/ApiClient'
import Spinner from 'react-bootstrap/Spinner'
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Alert from 'react-bootstrap/Alert'
import "./PlayerData.css"

function PlayerPage () {
	const [playerData, setPlayerData] = useState<PlayerData>({ magin: 0, currentSceneIdx: 0, sceneUnlockDate: 0, variables: { bools: {}, strings: {}, floats: {}}})
	const [error, setError] = useState('')

	const apiClient = useContext(ApiContext)

	const mutate = useCallback((field: string, value: any) => {
		setPlayerData(p => ({...p, [field]: value}))
	}, [])

	useEffect(() => {
		if (apiClient.session) apiClient.getData().then(data => setPlayerData(data)).catch(e => setError(e.response?.data ?? e.essage))
	}, [apiClient, apiClient.assumedAs, apiClient.session])

	if (!apiClient.session) {
		return <div className='playerPage'>Log in to continue</div>
	}

	if (!playerData) {
		return <div className='playerPage'>
			<Spinner/>
		</div>
	}
	return (
		<div className='playerPage'>
			{error ? <Alert variant="danger">{error}</Alert> : null}
			<Form>
				<h4>Core Data</h4>
				<hr/>
				<Form.Group as={Row} className="mb-3">
					<Form.Label column sm={3}>Magin</Form.Label>
					<Col sm={9}>
						<Form.Control type="number" value={playerData.magin} onChange={e => mutate('magin', parseInt(e.target.value))}/>
					</Col>
				</Form.Group>
				<Form.Group as={Row} className="mb-3">
					<Form.Label column sm={3}>Scene Index</Form.Label>
					<Col sm={9}>
						<Form.Control type="number" value={playerData.currentSceneIdx} onChange={e => mutate('currentSceneIdx', parseInt(e.target.value))}/>
					</Col>
				</Form.Group>
				<Form.Group as={Row} className="mb-3">
					<Form.Label column sm={3}>Scene Date</Form.Label>
					<Col sm={9}>
						<Form.Control type="input" onChange={e => mutate('sceneDate', new Date(e.target.value).getTime())} value={new Date(playerData.sceneUnlockDate).toLocaleString() }/>
					</Col>
				</Form.Group>
				<br/>
				<h4>Variables Data</h4>
				<hr/>
				<h5>Strings</h5>
				{
					Object.entries(playerData.variables.strings).map(([k, v]) => <Form.Group as={Row} className="mb-3" key={k}>
						<Form.Label column sm={3}>{k}</Form.Label>
						<Col sm={9}>
							<Form.Control type="input" value={v} disabled/>
						</Col>
					</Form.Group>)
				}
				<h5>Floats</h5>
				{
					Object.entries(playerData.variables.floats).map(([k, v]) => <Form.Group as={Row} className="mb-3" key={k}>
						<Form.Label column sm={3}>{k}</Form.Label>
						<Col sm={9}>
							<Form.Control type="number" value={v} disabled/>
						</Col>
					</Form.Group>)
				}
				<h5>Bools</h5>
				{
					Object.entries(playerData.variables.bools).map(([k, v]) => <Form.Group as={Row} className="mb-3" key={k}>
						<Form.Check
							checked={v}
							type="switch"
							label={k}
							readOnly
						/>
					</Form.Group>)
				}
			</Form>
		</div>
	)
}

export default PlayerPage